import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Card, Row, Table, Popconfirm, Col, Modal } from 'antd';
import FormImportDataStudent from './FormImportDataStudent';

const columns = [
  {
    title: 'SBD',
    dataIndex: 'sbd',
    key: 'sbd',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Giới tính',
    dataIndex: 'sex',
    key: 'sex',
  },
  {
    title: 'Ngày tuyển sinh',
    dataIndex: 'dateTS',
    key: 'dateTS',
  },
  {
    title: 'Trình độ tốt nghiệp',
    dataIndex: 'tdtb',
    key: 'tdtb',
  },
  {
    title: 'Hệ nhập học',
    dataIndex: 'hnh',
    key: 'hnh',
  },
  {
    title: 'Mã SV',
    dataIndex: 'masv',
    key: 'masv',
  },
  {
    title: 'Nơi sinh',
    dataIndex: 'placeOfBirth',
    key: 'placeOfBirth',
  },
];

class ImportDataStudentPage extends Component {
  state = { lstDataStudent: [] };
  uploadData = lstDataStudent => {
    this.setState({ lstDataStudent });
  };

  cancelUpload = () => {
    this.setState({
      lstDataStudent: [],
    });
  };

  render() {
    return (
      <Card title="Nhập khẩu dữ liệu">
        <Row>
          <Col span={24}>
            <FormImportDataStudent uploadData={this.uploadData} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={this.state.lstDataStudent} columns={columns} />
          </Col>
          <Col offset={8} span={12}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={this.cancelUpload} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ImportDataStudentPage />
  </Page>
);



