import React, { Component } from 'react';
import { Form, Button, Upload, Icon, Row, Col, Select } from 'antd';
import BtnSave from 'components/common/button/BtnSave';
import { importDataStudent } from 'dump/data';

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 4 },
  colon: false,
};

const { Option } = Select;
@Form.create({ name: 'formImportDataStudent' })
class FormImportDataStudent extends Component {
  state = {};
  normFile = e => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };
  handleSubmit = () => {
    this.props.uploadData(importDataStudent);
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Row>
        <Col span={24}>
          <Form onSubmit={this.handleSubmit}>
            <Form.Item label="File dữ liệu" {...formItemLayout}>
              {getFieldDecorator('upload', {
                valuePropName: 'fileList',
                getValueFromEvent: this.normFile,
              })(
                <Upload name="file" listType="picture">
                  <Button>
                    <Icon type="upload" /> Click to upload
                  </Button>
                </Upload>
              )}
            </Form.Item>
            <Form.Item label="Tên bảng" hasFeedback {...formItemLayout}>
              {getFieldDecorator('sheetName', {})(
                <Select placeholder="">
                  <Option value="china">Sinh viên</Option>
                  <Option value="usa">Giáo viên</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" title="Xem dữ liệu" />
            </Form.Item>
          </Form>
        </Col>
      </Row>
    );
  }
}

export default FormImportDataStudent;



